import React, { useEffect, useState } from 'react';
import style from '../scss/components/ScrollArrow.module.scss';

const arrowID = 'scroll-arrow';
const ScrollArrow: React.FC = () => {
  const [bottom, setBottom] = useState('10px');

  useEffect(() => {
    function onResize(): void {
      const arrow = document.getElementById(arrowID) as HTMLElement;
      arrow.style.left = (window.innerWidth - arrow.offsetWidth) / 2 + 'px';
      window.removeEventListener('resize', onResize);
    }

    function onScroll(): void {
      const arrow = document.getElementById(arrowID) as HTMLElement;
      const { scrollY } = window;

      if (scrollY > 2 * arrow.offsetHeight) {
        setBottom('-99999px');
        window.removeEventListener('scroll', onScroll);
      }

      setBottom(-window.scrollY + 'px');
    }

    onScroll();
    onResize();
    window.addEventListener('scroll', onScroll);
    window.addEventListener('resize', onResize);
  }, []);

  return (
    <div id={arrowID} className={style.arrowWrapper} style={{ bottom }}>
      <svg
        className={style.arrow}
        xmlns="http://www.w3.org/2000/svg"
        width="24"
        height="24"
        viewBox="0 0 24 24"
      >
        <path d="M0 7.33l2.829-2.83 9.175 9.339 9.167-9.339 2.829 2.83-11.996 12.17z" />
      </svg>
    </div>
  );
};

export default ScrollArrow;
